@import url(https://fonts.googleapis.com/css2?family=Nunito&family=Nunito+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  text-align: left;
}

h1 {
  padding: 0;
  margin-top: 0.4em;
  margin-left: 4.5vh;
  font-size: 35vh;
  color: var(--dark);
  text-align: justify;
  line-height: 120%;
  font-family: 'Nunito', sans-serif;
}


@media(max-width: 1200px){
  h1 {
    font-size: 15vh;
  }
}

@media(max-width: 750px){
  h1 {
    font-size: 11vh;
  }
}

@media(max-width: 600px){
  h1 {
    margin-top: 1em;
    font-size: 9.25vh;
  }
}
.container {
  display: grid;
}

.header {
  font-size: 12vh;
  color: var(--dark);
  text-align: left;
  margin-bottom: 0.6em;
}

.info > img {
  padding-right: 4em;
  max-width: 100%;
  height: auto;
  float: right;
}

.text {
  width: 67%;
  float: left;
}

.text > p {
  font-family: 'Nunito', sans-serif;
  text-align: left;
  font-size: 4vh;
  line-height: 2;
  margin-left: 4.5vh;
  width: inherit;
  color: var(--dark-light);
  margin-bottom: 1.2em;
}

