.container {
  display: grid;
}

.header {
  font-size: 12vh;
  color: var(--dark);
  text-align: left;
  margin-bottom: 0.6em;
}

#background-image {
  -webkit-filter: saturate(99%) brightness(25%) hue-rotate(215.68deg);
          filter: saturate(99%) brightness(25%) hue-rotate(215.68deg);
  width: 85%;
  height: 8.5em;
  object-fit: cover;
  border-radius: 1em;
  margin-bottom: 1.75em;
}

#project-item{
  position: relative;
}

#title, #description {
  position: absolute;
  background: none;
}

#title {
  top: 5%;
  left: 37.5%;
  width: 50%;
  font-size: 3.8em;
  font-family: 'Nunito', sans-serif;
  font-weight: 500;
  color: var(--background);
  text-align: right;
}

#description {
  font-size: 1.25em;
  color: var(--light);
  top: 55%;
  left: -12.5%;
  text-align: right;
  width: 100%;
}

@media(max-width: 700px){

  .header {
    font-size: 10vh;
    text-align: left;
  }

  #title {
    top: 12.5%;
    left: 7.5%;
    width: 80%;
    font-size: 2.75em;
    font-family: 'Nunito', sans-serif;
    font-weight: 500;
    color: var(--background);
    text-align: right;
  }

  #description {
    font-size: 1em;
    color: var(--light);
    top: 50%;
    left: -12.5%;
    text-align: right;
    width: 100%;
  }
}
#contact-header {
  text-align: center;
  font-size: 4em;
  margin-top: 1.5em;
  width: 97%;
}

#contact-text {
  color: var(--dark-light);
  text-align: center;
  width: 150%;
  white-space: pre-line;
  margin: 0;
  display: block;
}

.inner > a:hover {
  opacity: 0.5;
}

.inner > a > svg {
  margin-right: 2em;
  margin-left: 1em;
  x: 50%;
  width: 3.5em;
  height: auto;
  z-index:1;
  fill: var(--icon-fill);
}

.outer {
  overflow-x: hidden;
  height: auto;
}

.inner {
  position: absolute;
  width: 100%;
  margin-top: 9em;
  height: 1%;
}

button {
  padding: .35em;
  text-align: center;
  font-size: 1.5em;
  text-decoration: none;
  border-radius: 0.45em;
  border-color: var(--light);
  border-style: solid;
  border-width: 1.5px;
  margin-top: 4em;
  width: 7.5em;
  height: 3em;
}

button > a {
  text-align: center;
  font-size: 1.15em;
  text-decoration: none;
  color: var(--light);
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
}

button:hover {
  color: var(--background);
  background-color: var(--dark);
  border-color: var(--background);
}

.footer {
  position: relative;
  width: 100%;
  height: auto;
  border: none;
  bottom: 0;
  top: 0.25em;
  background-color: var(--dark);
}

.footer > p{
  color: var(--light);
  background-color: var(--dark);
  padding: 1em;
  margin-top: 5em;
}

#social-images {
  z-index: 1;
}

@media(max-width: 1000px){
  #contact-text{
    width: 100%;
  }
  #contact-text-container{
    width: 100%;
    font-size: 3vh;
  }

  #contact-header {
    text-align: center;
    font-size: 3.5em;
    margin-top: 1.5em;
    margin-left: 0.2em;
  }
}


.nav {
  overflow: hidden;
  background: var(--background);
  position: fixed;
  top: 0;
  width: 100%;
  height: 2.5%;
  list-style-type: none;
  text-transform: lowercase;
  margin: 0em;
  padding: 1.25em;
  display: block;
}


.nav > li {
  float: right;
  overflow: hidden;
  background: transparent;
  overflow: hidden;
  margin-right: 2em;
  width: auto;
  display: block;
}

li > a {
  padding:  1.05em;
  text-align: center;
  font-size: 1.15em;
  text-decoration: none;
  color: var(--light);
}

li > button {
  padding:  1.05em;
  text-align: center;
  font-size: 1.15em;
  text-decoration: none;
  color: var(--light);
}


li > a:hover {
  color: #005b96;
  cursor: pointer;
}

#home {
  float: left;
  font-weight: 20px;
  font-size: 1.15em;
}

#home > a, a:hover{
  color: var(--dark-light);
}

#action {
  border-radius: 0.5em;
  border-color: var(--light);
  border-style: solid;
  border-width: 2px;
  margin-right: 4em;
}

#action:hover {
  border-color: var(--dark);
}

#action > a:hover {
  color: var(--background);
  background-color: var(--dark);
}

/* Position and sizing of burger button */
.bm-burger-button {
    position: absolute;
    float: right;
    width: 5%;
    height: 2.5%;
    left: 85%;
    top: 4%;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: var(--dark);
  }
  
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background: #a90000;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: #bdc3c7;
  }
  
  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed;
    height: 100%;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #373a47;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #000000;
    padding: 0.8em;
  }
  
  /* Individual item */
  .bm-item {
    display: inline-block;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }

  /* Individual item */
.bm-item {
    display: inline-block;
    color: #d1d1d1;
    margin-bottom: 10px;
    text-align: left;
    text-decoration: none;
    transition: color 0.2s;
  }
  
  .bm-item:hover {
    color: #ffffff;
  }
:root {
  --background: #B3CDE0;
  --dark: #011F4B;
  --light: #6497B1;
  --dark-light: #03396C;
  --icon-fill: #010d1f;
}

* {
  text-align: center;
  background-color: #B3CDE0;
  background-color: var(--background);
  font-family: 'Nunito Sans', sans-serif;
  overflow: visible;
}

.App {
  text-align: center;
  background-color: #B3CDE0;
  background-color: var(--background);
  overflow-y: scroll;
  overflow-x: hidden;
  height: 100%;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

Home {
  margin-bottom: 90em;
}

#contact-section {
  height: 100%;
  overflow: auto;
}

#sidebar-section {
  visibility: hidden;
}


@media (max-width: 600px){
  .nav {
    position: fixed;
    top: 0;
    width: 150%;
    height: 2.5%;
    visibility: hidden;
  }

  #sidebar-section {
    visibility: visible;
  }

  #home {
    visibility: hidden;
  }

  li > a {
  text-align: center;
  font-size: .5em;
  }

  li > button {
  padding:  1.05em;
  text-align: center;
  font-size: 1.15em;
  text-decoration: none;
  color: #6497B1;
  }
}

